import { Component, OnInit } from '@angular/core';
import { Post } from '../interfaces'
import { PostserveService } from '../postserve.service'
import { ThrowStmt } from '@angular/compiler';
import { FormBuilder,FormGroup } from '@angular/forms'
import { MessageService } from 'primeng/api';

@Component({
  selector: 'app-writepost',
  templateUrl: './writepost.component.html',
  styleUrls: ['./writepost.component.css'],
  providers: [MessageService]
})
export class WritepostComponent implements OnInit {

  uploadForm:FormGroup;
  title:string;
  content:string;
  category:string;
  p_success:any;
  //file:any;
  step = 1;
  p_err:any;
  p_file:any;

  constructor(private formBuilder:FormBuilder,private postservice: PostserveService,private messageservice:MessageService) { }

  setStep(index: number) {
    this.step = index;
  }
  ngOnInit(): void {
    this.uploadForm = this.formBuilder.group({
      profile: [''],
      author: [''],
      title: [''],
      content:['']
    });
  }

  postentry(){
    const formData = new FormData();
    if(this.title && this.content && this.category){
      formData.append('category',this.category);
      formData.append('title',this.title);
      formData.append('content',this.content);
      if(this.uploadForm.get('profile').value)
      formData.append('postImage',this.uploadForm.get('profile').value);
      else{
        formData.append('postImage','');
      }
      console.log(formData);  
    this.postservice.addpost(formData).subscribe(res=>{
      console.log(res);
      this.p_success=1;
      this.messageservice.add({severity:'success',summary:'Post added successfuly!'});
      this.ngOnInit();
    });
    }
    else{
      console.log("Here");
      this.p_err=1;
      this.messageservice.add({severity:'success',summary:'Please fill the necessary details.'});
    }
  }

  onFileSelect(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      if(file['type']=='image/jpeg' || file['type']=='image/png')
        this.uploadForm.get('profile').setValue(file);
      else{
        this.messageservice.add({severity:'error',summary:'Please upload only Jpeg or PNG files.'});
      }
    }
  }

  // success(){
  //   if(this.p_success!=1)
  //     return true;
  //   else
  //     return false;
  // }
  // fillall(){
  //   if(this.p_err!=1)
  //     return true;
  //   else
  //     return false;
  // }
  // filerr(){
  //   if(this.p_file!=1)
  //     return true;
  //   else
  //     return false;
  // }
}
