import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders  } from '@angular/common/http'

import { Observable } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { FormBuilder,FormGroup } from '@angular/forms'

import { Post } from './interfaces'

@Injectable({
  providedIn: 'root'
})
export class PostserveService {
  baseURL: string = '/api/'

  constructor( private http: HttpClient) { }

  
  getposts(){
    return this.http.get(this.baseURL+'posts');
  }
  getwallposts(){
    return this.http.get(this.baseURL+'wallposts');
  }

  addpost(post:FormData){
      return this.http.post(this.baseURL+'post',post);
  }

  addcomment(data:any){
    return this.http.post(this.baseURL+'addcomment',data);
  }

  getuserposts(data:any){
    return this.http.post(this.baseURL+'viewposts',data);
  }

  upvote(data:any){
    return this.http.post(this.baseURL+'upvote',data);
  }

  upvotecancel(data:any){
    return this.http.post(this.baseURL+'cancelupvote',data);
  }

  deletepost(data:any){
    return this.http.post(this.baseURL+'deletepost',data);
  }

  explore(data:any){
    return this.http.post(this.baseURL+'explore',data);
  }
}
