import { Component, OnInit } from '@angular/core';
import { ProfileService } from '../profile.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Post, Profile } from '../interfaces';
import { PostserveService } from '../postserve.service';

@Component({
  selector: 'app-searchprofile',
  templateUrl: './searchprofile.component.html',
  styleUrls: ['./searchprofile.component.css']
})
export class SearchprofileComponent implements OnInit {

  constructor(
    private profileservice:ProfileService,
    private route:ActivatedRoute,
    private router:Router,
    private postservice:PostserveService,
    private actrouter:ActivatedRoute) {
      this.actrouter.paramMap.subscribe(res=>{
        this.showerr=false;
        this.search=res["params"]["search"];
        console.log(res);
        console.log(this.search)
        this.profileservice.viewuser({query:this.search}).subscribe((res:Profile)=>{
        console.log("View user response:",res);
        this.profile=res[0];
        console.log(this.profile);
        this.followid=res[0]['userid'];
        this.profileservice.getdata().subscribe((res1:Profile)=>{
        console.log("res1:",res1);
        // this.showerr=false;
        let arr=res1[0].following;
        console.log('arr: ',arr);
          if(arr.includes(this.followid))
          {
            console.log('here');
            this.isfollow=false;
            this.isunfollow=true;
          }
          else{
            this.isfollow=true;
            this.isunfollow=false;
          }
      });
      this.postservice.getuserposts({posts:this.followid}).subscribe((posts_returned:Post[])=>{
        this.entries=posts_returned
        console.log("Returned inside: ",posts_returned);
      });
  },(err)=>{
    //alert("User not found");
    console.log(err);
    this.showerr=true;
    //this.router.navigateByUrl('/home');
  })
})

   }
  search:string;
  followid:string;
  entries:Post[];
  profile:Profile;
  isfollow:boolean;
  isunfollow:boolean;
  proid:string;
  showfollowmsg:boolean=false;
  showerr:boolean=false;
  ngOnInit(): void {
  }

  follow(){
    this.profileservice.follow({follows:this.followid}).subscribe((res)=>{
      console.log(res);
      window.location.reload();
    },(err)=>
    console.log(err));
  }

  unfollow(){
    this.profileservice.unfollow({unfollowid:this.followid}).subscribe((res)=>{
      window.location.reload();
      console.log(res);
    },(err)=>{
      console.log(err);
    })
  }
}
