import { Component, OnInit, Input } from '@angular/core';
import { Post,IComment,Info } from '../interfaces'
import { ProfileService } from '../profile.service';
import { PostserveService } from '../postserve.service';
import { Router, NavigationEnd } from '@angular/router';
import { MatSnackBar} from '@angular/material/snack-bar'
import {MessageService} from 'primeng/api';
import { MatCardAvatar } from '@angular/material/card';
@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.css'],
  providers:[MessageService]
})
export class CardComponent implements OnInit {

  post: Post
  postid:string;
  comments:IComment[];
  comment:string;
  usernames:string[]=[];
  commentsuccess:boolean=false;
  upvote_err:boolean=false;
  proid:string;
  author:string;
  avatar:string;
  num_upvotes:number;
  add:boolean=false;
  subtract:boolean=false;
  icon:string;
  subchanged:boolean=false;
  addchanged:boolean=false;
  //mySubscription:any;
  constructor(private profileservice:ProfileService,
    private postservice:PostserveService,
    private router:Router,
    private messageservice:MessageService
    ) {
  }
  @Input() get posts(): Post {
    console.log("Post inside: ",this.post);
    return this.post;
}

    set posts(value: Post) {
      if (value) {
          this.post = value;
          this.comments=this.post.comments;
          //console.log("Post inside: ",this.post);
          //console.log("Comments: ",this.comments);
          var byid=this.post.userid;
          this.profileservice.getinfo({ids:byid}).subscribe(result=>{
           // console.log(result);
            this.author=result[0]["username"];
            this.avatar=result[0]["userimage"];
            //console.log("avatar: ",this.avatar);
          })
          let useridarr:string[]=[];
          if(this.comments.length){
          this.comments.forEach((element)=>{
            useridarr.push(element.by)
          });
          this.profileservice.getinfo({ids:useridarr}).subscribe((res:Info[])=>{
            useridarr.forEach((element)=>{
              const ind=res.findIndex((user)=>{
                return user.userid==element
              });
              this.usernames.push(res[ind].username);
            });
          });
        }
      }
}
  ngOnInit(): void {
    this.profileservice.getupvotes().subscribe((res)=>{
      let arr=res[0]['upvotes']
      if(arr.includes(this.post._id))
        this.icon="favorite";
      else
        this.icon="favorite_border"
    },(err)=>{
      console.log(err);
    })
    this.num_upvotes=this.post.upvotes;
    if(this.add)
      {
        if(this.subchanged)
          this.num_upvotes=this.post.upvotes;
        else{
        this.num_upvotes=this.num_upvotes+1;
        this.addchanged=true;
        }
        this.icon="favorite";
        this.add=false;
      }
    if(this.subtract){
      if(this.addchanged)
        this.num_upvotes=this.post.upvotes;
      else
        {
          this.num_upvotes=this.post.upvotes-1;
          this.subchanged=true;
        }
        this.icon="favorite_border"
        this.subtract=false;
      }
    this.profileservice.basicinfo().subscribe(res=>{
      this.proid=res[0]["userid"];
    })
  }
  submitcomment(){
    console.log(this.post,this.comment);
    let com = {
      _id:this.post._id,
      comment:this.comment
    }
    if(this.comment.length){
    this.postservice.addcomment(com).subscribe((res)=>{
      //console.log(res);
      this.commentsuccess=true;
    },(err)=>{
      console.log(err);
    });
  }
  }

  upvote(){
    //let a:MatSnackBar;
    console.log("Upvote button clicked");
    //a.openFromComponent('Hello');
    this.postservice.upvote({_id:this.post._id}).subscribe((res)=>{
      console.log(res);
      if(res["upvoted"]==1){
        //window.location.reload();
        // this.messageservice.add({
          // severity: 'success',
          // summary: 'Upvote added successfully'
          // detail: 'API Key or URL is invalid.',
      // });
      this.add=true;
      this.ngOnInit();
      }
      else if(res["upvoted"]==-1){
        this.upvote_err=true;
        this.postservice.upvotecancel({_id:this.post._id}).subscribe((result)=>{
          console.log(result);
          this.subtract=true;
          this.ngOnInit();
        },(err)=>{console.log(err);})
      }
    },(err)=>{
      console.log(err);
    })
  }

  check(){
    if(this.proid==this.post.userid){
      return true;
    }
    return false;
  }

  deletepost(){
    console.log("Here");
    let delimg: string;
    console.log("ID:",this.post._id);
    
    if(this.post.postImage){
      delimg=this.post.postImage.split('\\')[1]
    }
    else{
      delimg='';
    }
    console.log(delimg);
     this.postservice.deletepost({deleteid:this.post._id,deleteimg:delimg}).subscribe(res=>{
       console.log(res);
       alert('Post deleted');
       window.location.reload();
     })
  }
}
