import { Component, OnInit } from '@angular/core';
import { ProfileService } from '../profile.service';
import {Profile, Post} from '../interfaces';
import { PostserveService } from '../postserve.service';
import { ThrowStmt } from '@angular/compiler';
import { FormBuilder,FormGroup } from '@angular/forms'
import { MessageService } from 'primeng/api';
@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css'],
  providers: [MessageService]
})
export class ProfileComponent implements OnInit {

  constructor(
    private profileservice:ProfileService,
    private postservice:PostserveService,
    private formBuilder:FormBuilder,
    private messageservice:MessageService
    ) { }
  
  
  profile:Profile;
  usernames:any[];
  entries:Post[];
  
  
  uploadForm:FormGroup;
  ngOnInit(): void {
    this.uploadForm = this.formBuilder.group({
      profile: ['']
    });
    this.profileservice.getdata().subscribe((res:Profile)=>{
      this.usernames=[]
      console.log("profile: ",res[0]);
      this.profile=res[0];
      //console.log("Follows: ",this.profile.following);
      this.profileservice.getinfo({ids:this.profile.following}).subscribe((result:Profile)=>{
        console.log("result: ",result);
        for(var i in Object.values(result)){
          this.usernames.push(result[i].username)
        }
        this.postservice.getposts().subscribe((res:Post[])=>{
          this.entries=res;
        })
    })
    
  });
  }

  onFileSelect(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      if(file['type']=='image/jpeg' || file['type']=='image/png')
        this.uploadForm.get('profile').setValue(file);
      else{
        this.messageservice.add({severity:'error',summary:'Please upload only Jpeg or PNG files.'});
      }
    }
  }

  changedp(){
    const formData = new FormData();
    if(this.uploadForm.get('profile').value){
    formData.append('newImage',this.uploadForm.get('profile').value);
    this.profileservice.changedp(formData).subscribe(res=>{
      if(res['updated']==1)
        window.location.reload();
      else
        console.log(res);
    });
  }
  else
  {
    this.messageservice.add({severity:'error',summary:'Please upload valid image'});
  }
  }
}
