import { Component, OnInit } from '@angular/core';
import { AuthService } from '../auth.service';
import { Router } from '@angular/router';
import { ProfileService } from '../profile.service'
import { MessageService } from 'primeng/api';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css'],
  providers: [MessageService]
})
export class SignupComponent implements OnInit {

  constructor(
    private router:Router,
    private authservice:AuthService,
    private profileservice:ProfileService,
    private messageservice:MessageService) { }

  ngOnInit(): void {
  }
  email:string;
  password:string;
  c_password:string;
  show_err:any;
  name:string;
  gender:string;

  signup(){
    if(this.password==this.c_password){
      const user={email:this.email,password:this.password,username:this.name,gender:this.gender};
      //const userprofile={email:this.email,username:this.name,gender:this.gender};
      this.authservice.signup(user).subscribe(res=>{
        console.log(res);
        this.messageservice.add({severity:'error',summary:'Sign up successful'});
        this.router.navigateByUrl('');
      },(err)=>{
        this.show_err=1;
        console.log(err);
        this.messageservice.add({severity:'error',summary:'Sign up failed.Retry with valid and unique username and email'});
      })
      //this.profileservice.postdata(userprofile).subscribe();
    }
    else{
      this.show_err=1
      console.log("Passwords do not match");
      this.messageservice.add({severity:'error',summary:'Passwords do not match'});
    } 
    }
    showerr(){
      if(this.show_err!=1)
        return true;
      else
        return false;
    }  
  }
