import { Component, OnInit } from '@angular/core';
import { PostserveService } from '../postserve.service'
import { Post } from '../interfaces'

@Component({
  selector: 'app-wall',
  templateUrl: './wall.component.html',
  styleUrls: ['./wall.component.css']
})


export class WallComponent implements OnInit {

  constructor( private postservice: PostserveService) { }
  title:string;
  entries: Post[]=[];
  entries_feed: Post[];
  feed:any;
  ngOnInit(): void {
    this.title="Wall";
    this.postservice.getwallposts().subscribe((posts_returned:Post[])=>{
      this.entries=posts_returned;
      console.log("Returned: ",posts_returned);
    });
      //this.postservice.getposts().subscribe((posts_returned:Post[])=>{
      //  this.entries=posts_returned
      //  console.log("Returned: ",posts_returned);
      //});
  }
  
  showfeed(){
    return true;
  }
  showposts(){
    return true;
  }
}
