import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component'
import { SignupComponent } from './signup/signup.component';
import { ProfileComponent } from './profile/profile.component';
import { SearchprofileComponent } from './searchprofile/searchprofile.component';
import { IntroComponent } from './intro/intro.component';
import { ExploreComponent } from './explore/explore.component';

const routes: Routes = [
  {path:'',component: IntroComponent},
  {path:'home', component: HomeComponent},
  {path:'signup',component:SignupComponent},
  {path:'profile',component:ProfileComponent},
  {path:'searchprofile/:search',component:SearchprofileComponent,runGuardsAndResolvers: 'always'},
  {path:'explore/:category',component:ExploreComponent, runGuardsAndResolvers: 'always'},
  {path:'**',component: IntroComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes,{onSameUrlNavigation: 'reload'})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
