import { Component, OnInit } from '@angular/core';
import { AuthService } from '../auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ProfileService } from '../profile.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  search:string;
  username:string;
  email:string;
  display:boolean;
  opened:boolean=false;
  step=1;
  constructor(private authservice:AuthService,private router:Router,private profileservice:ProfileService,private actrouter:ActivatedRoute) { 
    // if(!this.loggedin){
    //   router.navigateByUrl('');
    // }
  }

  setStep(index: number) {
    this.step = index;
  }
  ngOnInit(): void {
    this.profileservice.basicinfo().subscribe((res)=>{
      this.username=res[0]["username"];
      this.email=res[0]["email"];
    },(err)=>{
      if(err["status"]==401){
        alert("Please login");
        this.router.navigateByUrl('');
      }
    })
  }
  
  finduser(){
    this.router.navigate(['/searchprofile',{queryParams:{email:this.search}}]);
  }
  logout(){
    this.authservice.logout();
    this.router.navigateByUrl('');
  }

  loggedin(){
    if(this.authservice.isLoggedIn)
      return true;
    else
      return false;
  }

  forest(){
    this.router.navigate(['/explore',{queryParams:{category:'Forest'}}]);
  }

  getprofile(){
    if(this.search)
      this.router.navigateByUrl('/searchprofile/'+this.search);
  }
}
